@import "../../../SCSS/.mixins";

.MainBlockCard{
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	gap: 5px;

	@media (max-width: 767px) {
		max-width: 179px;
	}


	&__icon{
		width: 100%;
		max-width: 96px;
		height: auto;
		max-height: 87px;
	}

	&__text{
		text-align: center;
	}
}